<template>
  <el-container>
    <el-header>
      <div>
        <img src="../assets/home.png" alt="" />
        <span>电商后台管理系统</span>
      </div>
      <el-button type="info" @click="logout">退出</el-button>
    </el-header>
    <el-container>
      <el-aside :width="isCollapse ? '64px' : '200px'">
        <div class="toggle_button" @click="toggleCollapse">|||</div>
        <el-menu
          background-color="#323743"
          text-color="#fff"
          active-text-color="#4393e9"
          unique-opened
          :collapse="isCollapse" :collapse-transition="false"
          router
          :default-active="NavState"
        >
          <!-- 一级菜单 -->
          <el-submenu
            :index="item.id + ''"
            v-for="item in menuList"
            :key="item.id"
          >
            <!-- 一级菜单模板 -->
            <template slot="title">
              <!-- 图标 -->
              <i :class="iconsObj[item.id]"></i>
              <!-- 文字 -->
              <span>{{ item.authName }}</span>
            </template>
            <!-- 二级菜单 -->
            <el-menu-item
              :index="'/'+subItem.path"
              v-for="subItem in item.children"
              :key="subItem.id"
              @click="saveNavState('/'+subItem.path)"
            >
              <i class="el-icon-menu"></i>
              <span>{{ subItem.authName }}</span>
            </el-menu-item>
          </el-submenu>
        </el-menu>
      </el-aside>
      <el-main>
        <router-view></router-view>
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
export default {
  name: 'Home',
  created() {
    this.getMenuList()
    this.NavState = window.sessionStorage.getItem('activePath')
  },
  data() {
    return {
      menuList: [],
      iconsObj: {
        125: 'el-icon-s-management',
        103: 'el-icon-s-tools',
        101: 'el-icon-s-goods',
        102: 'el-icon-s-order',
        145: 'el-icon-s-marketing'
      },
      isCollapse: false,
      NavState: ''
    }
  },
  methods: {
    // 退出事件
    logout() {
      // 清除sessionStorage中的token
      window.sessionStorage.clear()
      // 重定向到登录页面
      this.$router.push('/login')
    },
    async getMenuList() {
      const { data: res } = await this.$http.get('menus')
      if (res.meta.status !== 200) {
        return this.$message({
          type: 'error',
          message: res.meta.msg,
          showClose: true,
          center: true
        })
      }
      this.menuList = res.data
    },
    toggleCollapse() {
      this.isCollapse = !this.isCollapse
    },
    saveNavState(index) {
      window.sessionStorage.setItem('activePath', index)
      this.NavState = index
    }
  }
}
</script>

<style lang="less" scoped>
.el-container {
  height: 100%;
}

.el-header {
  display: flex;
  justify-content: space-between;
  padding: 10px 10px 10px 0px;
  background: #373d3f;
  > div {
    display: flex;
    align-items: center;
    font-size: 20px;
    color: #fff;
    img {
      width: 70px;
      height: 40px;
      margin-right: 10px;
    }
  }
}

.el-aside {
  background: #323743;
  .el-menu {
    border-right: none;
  }
  .toggle_button {
    line-height: 24px;
    font-size: 10px;
    color: #fff;
    letter-spacing: 0.2em;
    text-align: center;
    background: #4a5063;
    cursor: pointer;
    user-select: none;
  }
}

.el-main {
  background: #e9edf0;
}
</style>
