<template>
  <div class="login_container">
    <div class="login_box">
      <!-- 用户头像 -->
      <div class="avatar_box">
        <img src="../assets/logo.png" alt="logo">
      </div>
      <!-- 用户表单 -->
      <el-form ref="loginFormRef" :model="loginForm" :rules="loginFormRules" class="login_from">
        <el-form-item prop="username">
          <el-input v-model="loginForm.username" prefix-icon="el-icon-user"></el-input>
        </el-form-item>
        <el-form-item prop="password">
          <el-input v-model="loginForm.password" prefix-icon="el-icon-lock" type="password"></el-input>
        </el-form-item>
        <el-form-item class="btn">
            <el-button type="primary" @click="login">登录</el-button>
            <el-button type="info" @click="resetLoginForm">重置</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Login',
  data () {
    return {
      loginForm: {
        username: 'admin',
        password: '123456'
      },
      loginFormRules: {
        // 验证用户名是否合法
        username: this.$store.state.username,
        // 验证密码是否合法
        password: this.$store.state.password
      }
    }
  },
  methods: {
    // 重置表单
    resetLoginForm () {
      this.$refs.loginFormRef.resetFields()
    },
    login () {
      this.$refs.loginFormRef.validate(async valid => {
        // eslint-disable-next-line no-useless-return
        if (!valid) return
        const { data: res } = await this.$http.post('login', this.loginForm)
        if (res.meta.status !== 200) return this.$message({ message: '登录失败', type: 'error', showClose: true, center: true })
        this.$message({ message: '登录成功', type: 'success', showClose: true, center: true })
        // 将token保存到sessionStorage中
        window.sessionStorage.setItem('token', res.data.token)
        // 重定向到home页面
        this.$router.push('/home')
      })
    }
  }
}
</script>

<style lang="less" scoped>
.login_container {
  height: 100%;
  background: #2b4b6b;
}

.login_box {
  width: 450px;
  height: 300px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: #fff;
  border-radius: 10px;
  .avatar_box {
    position: absolute;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 130px;
    height: 130px;
    padding: 10px;
    background: #fff;
    border: 2px solid #eee;
    border-radius: 50%;
    box-shadow: 0 0 10px #ddd;
    img {
      width: 100%;
      height: 100%;
      background: #eee;
      border-radius: 50%;
    }
  }

  .login_from {
    position: absolute;
    bottom: 0;
    width: 100%;
    padding: 20px;
    .btn {
      display: flex;
      justify-content: flex-end;
    }
  }
}
</style>
